import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

class Header extends React.Component {
  //This opens the navbar dropdown
  navbarHamburgerToggleDropdown = () => {
    if (this.state.NavbarHamburgerIsOpen === 0) {
      this.setState({
        navbarHamburgerIsActive: "navbar-burger burger is-active",
        navbarMenuIsActive: "navbar-menu is-active",
        NavbarHamburgerIsOpen: 1,
      })
    } else {
      this.setState({
        navbarHamburgerIsActive: "navbar-burger burger",
        navbarMenuIsActive: "navbar-menu ",
        NavbarHamburgerIsOpen: 0,
      })
    }
  }

  constructor(props) {
    super();
    this.state = {
      siteTitle: props.siteTitle,
      navbarHamburgerIsActive: "navbar-burger burger",
      navbarMenuIsActive: "navbar-menu",
      NavbarHamburgerIsOpen: 0,
    }
  }

  render() {
    return (
      <header className="siteHeader">
        <nav className="navbar container" role="navigation" aria-label="main navigation">
          <div className="navbar-brand">
            <h1 className="navbar-item  is-size-1 has-text-justified">
              {/* can add in logo here */}
              <Link to="/" className="">
                {this.state.siteTitle}
              </Link>
            </h1>

            <Link
              role="button"
              className={this.state.navbarHamburgerIsActive}
              aria-label="menu"
              aria-expanded="false"
              to="#"
              data-target="navbarBasicExample"
              onClick={this.navbarHamburgerToggleDropdown}
            >
              <span aria-hidden="true" className="lightText"></span>
              <span aria-hidden="true" className="lightText"></span>
              <span aria-hidden="true" className="lightText"></span>
            </Link>
          </div>
          <div className={this.state.navbarMenuIsActive}>
            <div className="navbar-start">
              <Link className="navbar-item" to="/">
                Home
              </Link>
              <Link className="navbar-item" to="/blog">
                Blog
              </Link>
              <Link className="navbar-item" to="/aboutme">
                About Me
              </Link>
            </div>
          </div>
        </nav>
      </header>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
